import React, { useState } from 'react';
import PageContainer from '../../../components/layout/PageContainer';
import { useMutation, useQuery } from '@tanstack/react-query';
import { useRecoilValue } from 'recoil';
import dojangIdAtom from '../../../recoil/atom/dojangIdAtom';
import PaymentInfo from './components/PaymentInfo';
import ResistPayment from './components/ResistPayment';

const PaymentPage = () => {
  const dojangId = useRecoilValue(dojangIdAtom);

  // test
  const [isPaying, setIsPaying] = useState(true);

  // query
  // const { data } = useQuery(['getDojangPaymentInfo', dojangId], () => getDojangPaymentInfo(dojangId), {
  //   enabled: !!dojangId,
  // })

  return (
    <>
      <PageContainer width="sm">
        {isPaying ? <PaymentInfo setValue={setIsPaying} /> : <ResistPayment />}
      </PageContainer>
    </>
  );
};

export default PaymentPage;
