import React, { useState } from 'react';
import { Box, Typography } from '@mui/material';
import GrayBtnLong from '../../../../components/UI/Atoms/Button/GrayBtnLong';
import ModalContainer from '../../../../components/layout/ModalContainer';
import InfoLabelMD from '../../../../components/UI/Atoms/Info/InfoLabelMD';
import EditBtnSM from '../../../../components/UI/Atoms/Button/EditBtnSM';
import { useModal } from '../../../../hooks/basic/useModal';

const ResistPayment = () => {
  const [open, openFn, closeFn] = useModal();

  return (
    <>
      <div className="w-full flex flex-col gap-[10px] justify-between">
        <Typography variant="h3" gutterBottom className="mb-[20px]">
          결제 정보
        </Typography>
        <div className="px-[10px] flex flex-col gap-[10px]">
          <Box className="flex items-center justify-between h-[40px]">
            <InfoLabelMD className="text-[16px]">정기 결제</InfoLabelMD>
            <Box
              className="flex justify-between w-[334px]"
              sx={{
                width: 334,
                borderBottom: 1,
                borderColor: '#DDDDDD',
                gap: '10px',
                padding: '9px 16px',
              }}
            >
              <span>미신청</span>
              {/* <EditBtnSM onClick={openCancelFn}>해지</EditBtnSM> */}
            </Box>
          </Box>
        </div>
      </div>
      <div className="mt-[30px] mb-[80px]">
        {/* <GrayBtnLong
          onClick={openFn}
          className='w-[480px] h-[40px] bg-[#f2f2f2] border border-solid border-[#aaaaaa] text-[#555555] px-[190px]'
        >
          정기 결제 신청
        </GrayBtnLong> */}
        <GrayBtnLong
          disabled={true}
          className="w-[480px] h-[40px] bg-[#f2f2f2] border border-solid border-[#aaaaaa] text-[#555555] px-[10px]"
        >
          * 테스트 버전은 무료 입니다.
        </GrayBtnLong>
      </div>
      <div>
        <img
          src="https://tkdw-img.s3.ap-northeast-2.amazonaws.com/payment/event.png"
          alt="태권월드 정기결제 이벤트"
          className='w-full'
        />
      </div>

      <ModalContainer open={open} onClose={closeFn} title="정기 결제 신청">
        asd
      </ModalContainer>
    </>
  );
};

export default ResistPayment;
