import React from 'react';
import { Box, Typography } from '@mui/material';
import { useMutation } from '@tanstack/react-query';
import InfoLabelMD from '../../../../components/UI/Atoms/Info/InfoLabelMD';
import EditBtnSM from '../../../../components/UI/Atoms/Button/EditBtnSM';
import { Grid } from '../../../../components/common/Grid/Grid';
import { numberWithCommas } from '../../../../utils/lib/Regex';
import { useModal } from '../../../../hooks/basic/useModal';
import ModalContainerAlert from '../../../../components/layout/ModalContainerAlert';
import AlertText from '../../../../components/common/Alert/AlertText';
import ModalContainer from '../../../../components/layout/ModalContainer';

const PaymentInfo = ({ data, setValue }) => {
  // custom hook
  const [openCancel, openCancelFn, closeCancelFn] = useModal();
  const [openChange, openChangeFn, closeChangeFn] = useModal();

  // query
  // const { mutate: cancellationMT, isLoading } = useMutation()

  // handler
  const handleCancellation = (e) => {
    // cancellationMT(dojangId, {
    //   onSuccess: () => window.location.reload()
    // })
    setValue(false);
  };

  const payHistory = [
    { date: '2023-02-01', content: '태권월드 이용료', pay_amount: '10000' },
    { date: '2023-01-01', content: '태권월드 이용료', pay_amount: '50000' },
    { date: '2022-12-01', content: '태권월드 이용료', pay_amount: '50000' },
    { date: '2022-11-01', content: '태권월드 이용료', pay_amount: '50000' },
    { date: '2022-11-01', content: '태권월드 이용료', pay_amount: '50000' },
    { date: '2022-11-01', content: '태권월드 이용료', pay_amount: '50000' },
    { date: '2022-11-01', content: '태권월드 이용료', pay_amount: '50000' },
  ];

  return (
    <>
      <div className="w-full flex flex-col gap-[10px] justify-between">
        <Typography variant="h3" gutterBottom>
          결제 정보
        </Typography>
        <div className="px-[10px] flex flex-col gap-[10px] my-[20px]">
          <Box className="flex items-center justify-between h-[40px]">
            <InfoLabelMD className="text-[16px]">정기 결제</InfoLabelMD>
            <Box
              className="flex items-center justify-between w-[334px]"
              sx={{
                width: 334,
                borderBottom: 1,
                borderColor: '#DDDDDD',
                gap: '10px',
                padding: '9px 16px',
              }}
            >
              <span>신청</span>
              <EditBtnSM onClick={openCancelFn}>해지</EditBtnSM>
            </Box>
          </Box>
          <Box className="flex items-center justify-between h-[40px]">
            <InfoLabelMD className="text-[16px]">결제일</InfoLabelMD>
            <Box
              className="w-[334px]"
              sx={{
                width: 334,
                borderBottom: 1,
                borderColor: '#DDDDDD',
                gap: '10px',
                padding: '9px 16px',
              }}
            >
              매월 1일
            </Box>
          </Box>
          <Box className="flex items-center justify-between">
            <Box className="mt-[-45px]">
              <InfoLabelMD className="text-[16px]">카드 정보</InfoLabelMD>
            </Box>
            <Box
              className="flex flex-col h-[90px] justify-center"
              sx={{
                width: 334,
                borderBottom: 1,
                borderColor: '#DDDDDD',
                gap: '5px',
                padding: '9px 16px',
              }}
            >
              <div className="flex justify-between">
                <span>신한카드</span>
                {/* <EditBtnSM onClick={openChangeFn}>카드변경</EditBtnSM> */}
                <EditBtnSM >카드변경</EditBtnSM>
              </div>
              <div className="text-[16px] flex items-center">
                <span className="text-[12px] mr-[3px]">●●●● - ●●●● - ●●●● -</span>
                1234
              </div>
            </Box>
          </Box>
        </div>
        <Typography className="mt-[20px] mb-[20px]" variant="h3" gutterBottom>
          결제 이력
        </Typography>
        <div className="px-[10px]">
          <Grid payment>
            <Grid.RowWrap>
              {React.Children.toArray(
                payHistory?.map((item) => (
                  <Grid.Row payment>
                    <Grid.Col fixed="125px" className="pl-[10px]">
                      <div className="text-[16px]">{item?.date}</div>
                    </Grid.Col>
                    <Grid.Col fixed="210px">
                      <div className="text-[16px]">{item?.content}</div>
                    </Grid.Col>
                    <Grid.Col>
                      <div className="text-[16px]">
                        ￦ {numberWithCommas(Number(item?.pay_amount))}
                      </div>
                    </Grid.Col>
                  </Grid.Row>
                )),
              )}
            </Grid.RowWrap>
          </Grid>
        </div>
      </div>

      <ModalContainer open={openChange} onClose={closeChangeFn} title="카드 변경 등록">
        카드 변경
      </ModalContainer>

      <ModalContainerAlert
        open={openCancel}
        onClose={closeCancelFn}
        confirmText="해지"
        onConfirm={handleCancellation}
        height={400}
      >
        <AlertText title>해지 신청을 합니다.</AlertText>
        <AlertText>
          결제된 내역은 취소 불가하며, <br />
          이번달 말일 까지는 서비스를 이용 가능합니다. <br />
          <br />
          해지 상태가 되면 태권월드 서비스를 더이상 이용할 수 없게 됩니다. <br />
          해지 하시겠습니까? <br />
        </AlertText>
      </ModalContainerAlert>
    </>
  );
}

export default PaymentInfo;
